/* OFFSET */
$offset: 20;

/* Position padding & margin */
$position: top right bottom left;

/* float */
$float: left right;

/* Margin */
.no-margin {
  margin: 0 !important;
}

@for $i from 1 through $offset {
  .margin-#{5 * $i} {
      margin: 5px * $i !important;
  }

  @each $value in $position {
      .margin-#{$value}-#{5 * $i} {
          margin-#{$value}: 5px * $i !important;
      }
  }
}

/* Padding */
.no-padding {
  padding: 0 !important;
}

@for $i from 1 through $offset {
  .padding-#{5 * $i} {
      padding: 5px * $i !important;
  }

  @each $value in $position {
      .padding-#{$value}-#{5 * $i} {
          padding-#{$value}: 5px * $i !important;
      }
  }
}

@each $value in $float {
  .float-#{$value} {
      float: #{$value} !important; 
  }
}