#navigaton-wrapper {
  @include mobile() {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
  }
  .navbar {
    margin: 0;
    border-radius: 0;
    background-color: #171717;
    border-bottom: 1px solid #242424;
    z-index: 1000 !important;
  }
  .navbar-header {
    .navbar-brand {
      color: #fff;
      font-family: 'Montserrat-Bold';
    }
  }
  .navbar-collapse {
    text-align: center;
    .navbar-nav {
      text-align: center;
      .nav-item {
        padding: 10px 20px;
        .nav-link {
          padding: 2px 0;
          font-size: 12px;
          font-family: 'Montserrat-Regular';
          color: #fff;
          opacity: .90;
          letter-spacing: .125rem;
          text-transform: uppercase;
          &.active {
            border-bottom: 1px solid #fff;
            background-color: transparent;
          }
        }
      }
    }
  }
}