#portfolio {
  background-color: $dark-grey;
}

.work-filters {
  list-style: none;
  padding-left: 0;
  text-align: center;
  margin: 2rem auto;
  .filter-item {
    cursor: pointer;
    color: #fff;
    display: inline-block;
    padding: 8px 12px;
    margin: 0 8px;
    border-radius: 4px;
    text-transform: uppercase;
    font-size: 12px;
    font-family: 'Montserrat-SemiBold';
    transition: all ease .5s;
    &.active, &:hover {
      background-color: #fff;
      color: #000;
      transition: all ease .5s;
    }
  }
}

.work-posts {
  .item {
    position: relative;
    margin-bottom: 1.5rem;
  }
}