#profile {
  background-color: $dark;
  .jobs-name {
    background-color: #363636;
    padding: 1rem;
    text-align: center;
    opacity: .9;
    span {
      color: #fff;
      font-size: .75rem;
      font-family: 'Montserrat-SemiBold';
      text-transform: uppercase;
      letter-spacing: .125rem;
      line-height: 1.125rem;
    }
  }
  .profile-content {
    padding-left: 1rem;
    p {
      margin-top: 1.5rem;
      margin-bottom: 1rem;
    }
  }
  #contact-detail {
    h4 {
      font-family: 'Montserrat-Regular';
      font-size: 1.125rem;
    }
  }
}