/*============== CLASS HELPER ==============*/

/* BACKGROUND */
.bg-cover, .bg-parallax {
  background-position: center center!important;
  background-repeat: no-repeat!important;
  background-size: cover!important;
}

.bg-gray-dark {
  background-color: #171717!important;
}

.bg-overlay:before {
  content: '';
  display: block;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  background: rgba(0,0,0,.5)!important;
}

/* SIZING */
.full-height {
  height: 100% !important;
}

.full-width {
  width: 100% !important;
}

.clearfix {
  &:after {
    content: "";
    display: block;
    clear: both;
  }
}

/* TEXT */
.title-xl {
  font-size: 2.25rem!important;
  line-height: 2.875rem!important;
}

.text-large {
  font-size: 1rem!important;
  line-height: 1.5rem!important;
}

.text-xl {
  font-size: 1.125rem!important;
  line-height: 1.688rem!important;
}

.text-grey {
  color: $grey!important;
}

.text-white {
  color: #fff!important;
}

/* BUTTON */
.btn-large {
  font-size: .9rem;
  line-height: 1.125rem;
  padding: 1.125rem 1.875rem;
}

/* SEPARATOR LINE */
.sep-line-small {
  display: block;
  background-color: $red;
  width: 30px;
  height: 6px;
  margin-top: 1.5rem;
}

.sep-line-medium {
  display: block;
  background-color: $red;
  width: 80px;
  height: 6px;
  margin: 0 auto;
  margin-top: 1.5rem;
}